'use client';

import { useState, useRef, useEffect } from 'react';
import { Button } from './ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from './ui/card';
import { Input } from './ui/input';
import { ScrollArea } from './ui/scroll-area';
import { Sparkles, Send, X, Bot, User, Loader2 } from 'lucide-react';
import { cn } from '@/lib/utils';
import { chat } from '@/ai/flows/chatFlow';
import { MessageData } from 'genkit';

export function ChatBot() {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<MessageData[]>([
    { role: 'model', content: [{ text: "Hello! I'm your Abacus Pro assistant. How can I help you today?" }] },
  ]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollAreaRef.current) {
      scrollAreaRef.current.scrollTo({
        top: scrollAreaRef.current.scrollHeight,
        behavior: 'smooth',
      });
    }
  }, [messages]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim() || isLoading) return;
  
    const userMessage: MessageData = { role: 'user', content: [{ text: input }] };
    setMessages((prev) => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);
  
    try {
      const stream = await chat({ history: [...messages, userMessage] });
      
      if (!stream) {
        throw new Error('No stream returned from chat function');
      }
  
      let modelResponse = '';
      
      setMessages((prev) => [...prev, { role: 'model', content: [{ text: '' }] }]);
  
      try {
        if (typeof stream[Symbol.asyncIterator] === 'function') {
          for await (const chunk of stream) {
            
            if (chunk?.role === 'model' && chunk.content?.[0]?.text !== undefined) {
              const chunkText = chunk.content[0].text;
              
              setMessages((prev) => {
                const newMessages = [...prev];
                const lastIndex = newMessages.length - 1;
                if (lastIndex >= 0 && newMessages[lastIndex].role === 'model') {
                  newMessages[lastIndex] = { 
                    role: 'model', 
                    content: [{ text: chunkText }] 
                  };
                }
                return newMessages;
              });
            }
          }
        } else {
          throw new Error('Stream does not implement async iterator');
        }
      } catch (streamError: any) {
        console.error('Stream processing error:', streamError);
        throw streamError;
      }
  
    } catch (error: any) {
      console.error('Chat error details:', {
        message: error.message,
        stack: error.stack,
        name: error.name
      });
      
      setMessages((prev) => {
        const filtered = prev.filter((msg, index) => {
          return !(index === prev.length - 1 && msg.role === 'model' && msg.content[0].text === '');
        });
        
        return [
          ...filtered,
          { 
            role: 'model', 
            content: [{ 
              text: `Sorry, I encountered an error. Please check the browser console for more details and try again.` 
            }] 
          },
        ];
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      <div className={cn("fixed bottom-6 right-6 z-50 transition-transform duration-300", isOpen ? "scale-0" : "scale-100")}>
        <Button 
          size="icon" 
          className="w-16 h-16 rounded-full shadow-lg bg-gradient-to-tr from-primary to-primary/70 text-primary-foreground transition-transform hover:scale-110" 
          onClick={() => setIsOpen(true)}
        >
          <Sparkles className="w-8 h-8" />
          <span className="sr-only">Open Chat</span>
        </Button>
      </div>

      <div className={cn("fixed bottom-6 right-6 z-50 w-full max-w-sm transition-all duration-300", isOpen ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10 pointer-events-none")}>
        <Card className="flex flex-col h-[60vh] shadow-2xl">
          <CardHeader className="flex flex-row items-center justify-between">
            <div className="flex items-center gap-3">
              <Bot className="w-6 h-6 text-primary" />
              <CardTitle>Abacus Pro Assistant</CardTitle>
            </div>
            <Button variant="ghost" size="icon" onClick={() => setIsOpen(false)}>
              <X className="w-5 h-5" />
            </Button>
          </CardHeader>
          <CardContent className="flex-grow overflow-hidden">
            <ScrollArea className="h-full pr-4" ref={scrollAreaRef}>
              <div className="space-y-4">
                {messages.map((msg, index) => (
                  <div key={index} className={cn("flex items-start gap-3", msg.role === 'user' ? 'justify-end' : '')}>
                    {msg.role === 'model' && <Bot className="w-6 h-6 text-primary flex-shrink-0" />}
                    <div className={cn("rounded-lg px-3 py-2 max-w-[80%]", msg.role === 'model' ? 'bg-muted' : 'bg-primary text-primary-foreground')}>
                      <p className="text-sm">{msg.content[0].text}</p>
                    </div>
                    {msg.role === 'user' && <User className="w-6 h-6 text-secondary flex-shrink-0" />}
                  </div>
                ))}
                {isLoading && (
                  <div className="flex items-start gap-3">
                     <Bot className="w-6 h-6 text-primary flex-shrink-0" />
                     <div className="bg-muted rounded-lg px-3 py-2">
                        <Loader2 className="w-5 h-5 animate-spin text-primary" />
                     </div>
                  </div>
                )}
              </div>
            </ScrollArea>
          </CardContent>
          <CardFooter>
            <form onSubmit={handleSubmit} className="flex w-full items-center gap-2">
              <Input
                value={input}
                onChange={(e) => setInput(e.target.value)}
                placeholder="Ask a question..."
                disabled={isLoading}
              />
              <Button type="submit" size="icon" disabled={isLoading}>
                <Send className="w-5 h-5" />
              </Button>
            </form>
          </CardFooter>
        </Card>
      </div>
    </>
  );
}
